// --------------------
// Style reusable for all components

body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: white; // Main background Colour;
    color: black; // main body text colour
}

.info {
    margin: 20px;
    padding: 10px 50px;
    border-radius: 10px;
    display: inline-block;
    background-color: #5776e9;
    color: white;
}

.info.info-danger {
    background-color: #8a8383; // Unauthorized login Sign
}

// --------------------
// Style for App component
.App {
    text-align: center;

    .Title-bar {
        display: flex;
        // justify-content: space-between;
    }

    // .App-logo {
    //   animation: App-logo-spin infinite 20s linear;
    //   height: 8vw; // App logo size - responsive
    //   justify-content: flex-start;
    //   margin-right: 19vw;
    //   margin-top: 8px;

    // }

    .App-title {
        // font-size: 5vw; // titles font size
        font-size: calc(10px + 2vw);
        flex-grow: 1;
        margin-top: 5px;
        margin: 10px;
        padding: 10px;

        a {
            color: rgb(0, 0, 0); // nav bar Text colour
            // vertical-align: middle;
            // font-size: 3vw; // Nav bar font responsive size
            font-weight: bold;
            text-decoration: unset;
        }

        a.active {
            font-weight: bold;
        }
        // display: flex;
        // justify-content: flex-end;
    }

    .break {
        flex-grow: 8;
    }

    .signup-nav {
        // margin-top: 10px;
        padding-top: 15px;
        margin: 5px;
        // padding: 10px;
        // justify-content: flex-end;
        flex-grow: 1;
    }

    .login-nav {
        // margin-top: 10px;
        padding-top: 15px;
        margin: 5px;
        // padding: 5px;
        // justify-content: flex-end;
        flex-grow: 1;
    }

    .App-header {
        width: 100%;
        height: 10%;
        z-index: 999;
        top: 0; // puts nav bar at bottom of page
        background-color: $primary;
        // padding: 10px;
        color: rgb(0, 0, 0); // heading Text colour
        // margin: 10px;
        padding: 2px 10px;
        // border-radius: 10px; // Rounded corners
        box-shadow: 0 0 10px rgba(228, 227, 227, 0.8); // Gives top panel a shadow
    }

    .App-intro {
        font-size: calc(10px + 3vw);
    }

    // @keyframes App-logo-spin {
    //   from {
    //     transform: rotate(0deg);
    //   }

    //   to {
    //     transform: rotate(360deg);
    //   }
    // }
}

.highNav {
    text-align: center;
    height: 5vw;
    background-color: white;
    // margin: 10px;
    padding: 2px 10px;
    // border-radius: 10px; // Rounded corners
    // box-shadow:0 0 15px rgba(197, 197, 197, 0.8); // Gives bottom Nav panel a shadow

    a {
        color: rgb(0, 0, 0); // nav bar Text colour
        padding: 0 5px;
        font-size: calc(7px + 1vw); // Nav bar font responsive size
        text-decoration: unset;
    }

    a.active {
        font-weight: bold;
    }
}

.nav {
    position: absolute;
    bottom: 0; // puts nav bar at bottom of page
    width: 100%;
}

.lowNav {
    position: fixed;
    bottom: 0; // puts nav bar at bottom of page
    width: 100%;
    text-align: center;
    // height: 3vw;
    background-color: white;
    // margin: 10px;
    padding: 10px 10px;
    // border-radius: 10px; // Rounded corners
    box-shadow: 0 0 15px rgba(197, 197, 197, 0.8); // Gives bottom Nav panel a shadow

    a {
        color: rgb(0, 0, 0); // nav bar Text colour
        padding: 0 5px;
        font-size: calc(7px + 1vw); // Nav bar font responsive size
        text-decoration: unset;
    }

    a.active {
        font-weight: bold;
    }
}

// --------------------
// Style for Home component
.Home {
    p {
        font-size: calc(10px + 1vw);
    }
}

// --------------------
// Style for the text on the test homepage
