//**********TEXT BOX STYLING**********
.search {
    // width: 100%;
    position: relative;

    // display: flex;
}

.searchTerm {
    width: 40%;
    /*  border: 3px solid rgba(228, 227, 227, 0.8); */
    border-radius: none;
    padding: 5px;
    margin-top: 26px;
    height: 20px;
    /* outline: none; */
    color: rgba(228, 227, 227, 0.8);
}

.searchTerm:focus {
    color: black;
}

.searchButton {
    width: 60px;
    height: 35px;
    border: none;
    background: rgba(228, 227, 227, 0.8);
    text-align: center;
    color: black;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 16px;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
    width: 30%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
//**********END OF TEXT BOX STYLING**********

.searchBar {
    width: 40%;
    border: 3px solid #ff8dda;
    // border-right: none;
    padding: 5px;
    margin: 5px;
    height: 20px;
    border-radius: 5px 5px 5px 5px;
    outline: none;
    color: #9dbfaf;
    // box-shadow: 0 0 5px rgba(0, 0, 0, 0.8); // Schon ODER was?!? Gives bottom Nav panel a shadow
}

